<template>
	<div class="Index" v-loading="loading" element-loading-text="拼命加载中" element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)">
		<Header class="header"></Header>
		<el-container direction="horizontal">
			<el-aside :style="navBar">
				<Nav class="navBar"></Nav>
			</el-aside>
			<el-main :style="mainHeight">
				<div :style="currentHeight" class="box">
					<div class="text">
						<article :style="textBoxMargin" class="context" v-html="compiledMarkdown"></article>
					</div>
				</div>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	import Header from "./Docs_Header.vue"
	import Nav from "./Docs_NavBar.vue"
	import marked from 'marked'
	import hljs from "highlight.js"
	import 'highlight.js/styles/monokai-sublime.css'

	export default {
		name: 'Index',
		data() {
			return {
				currentHeight: {
					height: ''
				},
				textBoxMargin: {
					margin: ''
				},
				navBar:{
					width:'auto',
					height:'',
				},
				routeParams: "introduction",
				articleDetail: "正在加载...",
				screenWidth:window.innerWidth,
				mainHeight:{
					padding:0,
					height:'',
				},
				loading:true,
			}
		},
		components: {
			Header,
			Nav
		},
		mounted() {
			this.setInit();

			//屏幕监听
			window.onresize =()  =>{
	         return (()=>{
	           this.screenWidth = window.innerWidth;
	         })()
	       }

			marked.setOptions({
				renderer: new marked.Renderer(),
				highlight: function(code) {
					return hljs.highlightAuto(code).value;
				},
				pedantic: false,
				gfm: true,
				tables: true,
				breaks: true,
				sanitize: true,
				smartLists: true,
				smartypants: true,
				xhtml: true
			})
		},
		methods: {
			getData() {
				this.loading = true;
				this.$axios({
					method: "get",
					url: "../../static/" + this.routeParams + ".md",
				}).then(res => {
					console.log(res);
					this.articleDetail = res.data;
					this.loading = false;
				})
			},
			setInit(){
				let $Height = window.innerHeight - 70;
				console.log("当前可视高度：", $Height);
				this.currentHeight.height = $Height + "px";
				this.mainHeight.height = $Height+"px";
				this.navBar.height = $Height+"px";
				let $Width = window.innerWidth;
				console.log("当前可视宽度：", $Width);

				if ($Width < 768) {
					this.navBar.width = "auto";
				}else{
					this.navBar.width = "300px";
				}
			}
		},
		created() {
			if (sessionStorage.getItem('route') !== null) {
				this.routeParams = sessionStorage.getItem('route');
				this.getData();
			} else {
				this.getData();
			}
		},
		computed: {
			compiledMarkdown() {
				//this.articleDetail数据
				console.log(this.articleDetail);
				return marked(this.articleDetail, {
					sanitize: true
				})
			}
		},
		watch: {
			'$route': function(to, from) {
				console.log(this.$route.params.docs);
				this.routeParams = this.$route.params.docs;
				this.getData();
			},
			screenWidth(val){
			 this.setInit();
		   }
		}
	}
</script>
<style scoped>
	.navBar {
		min-width: 64px;
		overflow-x: hidden;
	}

	.el-aside {
		width: auto;
	}

	.box {
		height: auto;
		/* overflow: scroll; */
		overflow-x: hidden;
		color: #303133;
		background: #fafafa;
	}

	.context {
		margin: 20px;
		border: none;
	}
</style>



