<template>
    <div id="app">
        <router-view/>
    </div>
</template>

<script>
    export default {
        name: 'App'
    }
</script>

<style>
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
    }

    pre {
        background: #272822;
        color: white;
        height:auto;
        padding: 10px;
        overflow:scroll;
        overflow-y:hidden;
        padding:30 10px;
		border-radius: 3px;
    }

    code {
        font-family: SFMono-Regular, Menlo, Monaco, Consolas,微软雅黑, "Liberation Mono", "Courier New", monospace;
        background:#272822;
        color:white;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        border-radius: 3px;
    }
    li>code,p>code{
        padding:0 5px;
    }
	table {
		border:1px solid #f0f0f0;
		width:100%;
		border-spacing: 0;
		text-align: center;
	}
	tr>th{
		height:40px;
		background:#f5f5f5;
	}
	tr>td{
		height:35px;
	}
	table>tr,th,td{
		border:1px solid #f0f0f0;
		padding:0;
		margin:0;
	}
	img{
		width:100%;
	}
    p,li{
        line-height:2;
    }
</style>



