<script>
    export default {
        name: 'HelloWorld',
        data() {
            return {
                currentHeight: {
                    height: ''
                },
                route_name: '/view/introduction',
				side:false,
            }
        }, mounted() {
            let $Height = window.innerHeight - 70;
            console.log("当前可视高度：", $Height);
            this.currentHeight.height = $Height + "px";
			// let $Width = window.innerWidth;
			// console.log("当前可视宽度：", $Width);
			//
			// if ($Width < 768) {
			// 	this.side = true;
			// }
        }, methods: {}, watch: {
            '$route': function (to, from) {
                console.log("导航栏获取当前路由", this.$route.params.docs);
                sessionStorage.setItem("route", this.$route.params.docs);
                // this.routeParams = this.$route.params.docs;
                //    this.getData();
            }
        }, beforeMount() {
            if (sessionStorage.getItem('route') != null) {
                this.route_name = "/view/" + sessionStorage.getItem('route');
            }
        }
    }
</script>
<style scoped>

</style>



