<template>
	<div class="header">
		<div class="appName" @click="toIndex">
			PHPWord开发者文档-中文版
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{

			}
		},methods:{
			toIndex(){
				this.$router.go({
				  path:"/"
				})
			}
		}
	}
</script>

<style scoped>
	.header{
		width:100%;
		height:70px;
		background:#303643;
	}
	.appName{
		line-height:70px;
		color:white;
		font-size: 19px;
		padding-left:25px;
		display: inline-block;
		cursor: pointer;
	}
</style>



