import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Docs_Index'
import NavBars from '@/components/Docs_NavBar'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
			children:[
				{
					path:'view/:docs',
					component:Index
				}
			]
    }
  ]
})



